<template>
  <div class="hello">
    <Header></Header>

    <el-container>
      <el-card class="center-card">
        <el-button type="text" class="add-cat" @click="addTeam">{{$t('add_team')}}</el-button>
        <el-button type="text" class="goback-btn" @click="goback">{{$t('goback')}}</el-button>
        <el-table
          align="left"
          :empty-text="$t('empty_team_tips')"
          :data="list"
          height="400"
          style="width: 100%"
        >
          <el-table-column prop="teamname" :label="$t('team_name')"></el-table-column>
          <el-table-column prop="memberCount" :label="$t('memberCount')">
            <template slot-scope="scope">
              <router-link :to="'/team/member/'+scope.row.ID">{{scope.row.memberCount}}</router-link>
            </template>
          </el-table-column>
          <el-table-column prop="itemCount" :label="$t('itemCount')">
            <template slot-scope="scope">
              <router-link :to="'/team/item/'+scope.row.ID">{{scope.row.itemCount}}</router-link>
            </template>
          </el-table-column>
          <el-table-column prop :label="$t('operation')">
            <template slot-scope="scope">
              <el-button
                @click="$router.push({path:'/team/member/'+scope.row.ID})"
                type="text"
                size="small"
              >{{$t('member')}}</el-button>
              <el-button
                @click="$router.push({path:'/team/item/'+scope.row.ID})"
                type="text"
                size="small"
              >{{$t('team_item')}}</el-button>
              <el-button @click="edit(scope.row)" type="text" size="small">{{$t('edit')}}</el-button>
              <br />
              <el-button @click="attornDialog(scope.row)" type="text" size="small">{{$t('attorn')}}</el-button>
              <el-button @click="deleteTeam(scope.row.ID)" type="text" size="small">{{$t('delete')}}</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <el-dialog :visible.sync="dialogFormVisible" width="300px" :close-on-click-modal="false">
        <el-form>
          <el-form-item :label="$t('team_name')+':'">
            <el-input v-model="MyForm.teamname"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">{{$t('cancel')}}</el-button>
          <el-button type="primary" @click="MyFormSubmit">{{$t('confirm')}}</el-button>
        </div>
      </el-dialog>

      <el-dialog
        :visible.sync="dialogAttornVisible"
        :modal="false"
        width="300px"
        :close-on-click-modal="false"
      >
        <el-form>
          <el-form-item label>
            <el-input
              :placeholder="$t('attorn_username')"
              auto-complete="new-password"
              v-model="attornForm.username"
            ></el-input>
          </el-form-item>
          <el-form-item label>
            <el-input
              type="password"
              auto-complete="new-password"
              :placeholder="$t('input_login_password')"
              v-model="attornForm.password"
            ></el-input>
          </el-form-item>
        </el-form>
        <p class="tips">{{$t('attornTeamTips')}}</p>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogAttornVisible = false">{{$t('cancel')}}</el-button>
          <el-button type="primary" @click="attorn">{{$t('attorn')}}</el-button>
        </div>
      </el-dialog>
    </el-container>

    <Footer></Footer>
  </div>
</template>

<script>
export default {
  components: {},
  data () {
    return {
      MyForm: {
        id: '',
        teamname: ''
      },
      list: [],
      dialogFormVisible: false,
      dialogMemberVisible: false,
      dialogAttornVisible: false,
      attornForm: {
        team_id: '',
        username: '',
        password: ''
      }
    }
  },
  methods: {
    geList () {
      var that = this
      var url = this.DocConfig.server + '/team/list'
      var params = new URLSearchParams()
      that.$http.post(url, params).then(function (response) {
        if (response.data.status === 200) {
          var Info = response.data.data
          that.list = Info
        } else {
          that.$alert(response.data.message)
        }
      })
    },
    MyFormSubmit () {
      var that = this
      var url = this.DocConfig.server + '/team/save'
      var params = new URLSearchParams()
      params.append('id', this.MyForm.id)
      params.append('team_name', this.MyForm.teamname)
      that.$http.post(url, params).then(function (response) {
        if (response.data.status === 200) {
          that.dialogFormVisible = false
          that.geList()
          that.MyForm = {
            id: '',
            teamname: ''
          }
        } else {
          that.$alert(response.data.message)
        }
      })
    },
    edit (row) {
      this.MyForm.id = row.ID
      this.MyForm.teamname = row.teamname
      this.dialogFormVisible = true
    },
    deleteTeam (ID) {
      var that = this
      var url = this.DocConfig.server + '/team/delete'
      this.$confirm(that.$t('confirm_delete'), ' ', {
        confirmButtonText: that.$t('confirm'),
        cancelButtonText: that.$t('cancel'),
        type: 'warning'
      }).then(() => {
        var params = new URLSearchParams()
        params.append('id', ID)
        that.$http.post(url, params).then(function (response) {
          if (response.data.status === 200) {
            that.geList()
          } else {
            that.$alert(response.data.message)
          }
        })
      })
    },
    addTeam () {
      this.MyForm = {
        id: '',
        teamname: ''
      }
      this.dialogFormVisible = true
    },
    goback () {
      this.$router.push({ path: '/item/index' })
    },
    attornDialog (row) {
      this.attornForm.team_id = row.ID
      this.dialogAttornVisible = true
      console.log(row)
    },
    attorn () {
      var that = this
      var url = this.DocConfig.server + '/team/attorn'
      var params = new URLSearchParams()
      params.append('team_id', this.attornForm.team_id)
      params.append('username', this.attornForm.username)
      params.append('password', this.attornForm.password)
      that.$http.post(url, params).then(function (response) {
        if (response.data.status === 200) {
          that.dialogAttornVisible = false
          that.geList()
        } else {
          that.$alert(response.data.message)
        }
      })
    }
  },
  mounted () {
    this.geList()
  },
  beforeDestroy () {}
}
</script>

