<template>
  <div class="hello">
    <el-form ref="form" :model="form" label-width="150px">
      <el-form-item :label="$t('register_open')">
        <el-switch v-model="form.register_open"></el-switch>
      </el-form-item>
      <!-- 待支持
      <el-form-item label="所有人可以新建项目">
        <el-switch v-model="form.register_open"></el-switch>
      </el-form-item>
      -->
      <el-form-item :label="$t('home_page')">
        <el-select v-model="form.home_page" :placeholder="$t('please_choose')" disabled>
          <el-option :label="$t('full_page')" value="1"></el-option>
          <el-option :label="$t('login_page')" value="2"></el-option>
          <el-option :label="$t('jump_to_an_item')" value="3"></el-option>
          <!-- <el-option label="展示全站项目" value="4"></el-option> -->
        </el-select>
      </el-form-item>

      <el-form-item :label="$t('jump_to_item')" v-show="form.home_page == 3">
        <el-select v-model="form.home_item" :placeholder="$t('please_choose')">
          <el-option
            v-for="item in itemList"
            :label="item.item_name"
            :value="item.item_id"
            :key="item.item_id"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item :label="$t('ldap_open_label')">
        <el-switch v-model="form.ldap_open" disabled></el-switch>
      </el-form-item>

      <div v-if="form.ldap_open" style="margin-left:50px">
        <el-form-item label="ldap host">
          <el-input v-model="form.ldap_form.host" class="form-el"></el-input>
        </el-form-item>

        <el-form-item label="ldap port">
          <el-input v-model="form.ldap_form.port" style="width:90px"></el-input>
        </el-form-item>

        <el-form-item label="ldap base dn ">
          <el-input
            v-model="form.ldap_form.base_dn"
            class="form-el"
            placeholder="例如 dc=showdoc,dc=com"
          ></el-input>
        </el-form-item>

        <el-form-item label="ldap bind dn ">
          <el-input
            v-model="form.ldap_form.bind_dn"
            class="form-el"
            placeholder="cn=admin,dc=showdoc,dc=com"
          ></el-input>
        </el-form-item>

        <el-form-item label="ldap bind password ">
          <el-input v-model="form.ldap_form.bind_password" class="form-el" placeholder="例如 123456"></el-input>
        </el-form-item>

        <el-form-item label="ldap version">
          <el-select v-model="form.ldap_form.version" class="form-el">
            <el-option label="3" value="3"></el-option>
            <el-option label="2" value="2"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="ldap user filed">
          <el-input
            v-model="form.ldap_form.user_field"
            class="form-el"
            placeholder="例如 cn 或者 sAMAccountName"
          ></el-input>
        </el-form-item>
      </div>

      <el-form-item :label="$t('oss_open')">
        <el-switch v-model="form.oss_open" disabled></el-switch>
      </el-form-item>

      <div v-if="form.oss_open" style="margin-left:50px">
        <el-form-item :label="$t('oss_server')">
          <el-select v-model="form.oss_setting.oss_type">
            <el-option :label="$t('aliyun')" value="aliyun"></el-option>
            <el-option :label="$t('qiniu')" value="qiniu"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="key">
          <el-input v-model="form.oss_setting.key" class="form-el"></el-input>
        </el-form-item>

        <el-form-item label="secret">
          <el-input v-model="form.oss_setting.secret" class="form-el"></el-input>
        </el-form-item>

        <el-form-item label="endpoint" v-if="form.oss_setting.oss_type == 'aliyun'">
          <el-input v-model="form.oss_setting.endpoint" class="form-el"></el-input>
        </el-form-item>

        <el-form-item label="bucket">
          <el-input v-model="form.oss_setting.bucket" class="form-el"></el-input>
        </el-form-item>

        <el-form-item :label="$t('oss_domain')">
          <el-select v-model="form.oss_setting.protocol" style="width:100px;">
            <el-option label="http://" value="http"></el-option>
            <el-option label="https://" value="https"></el-option>
          </el-select>
          <el-input v-model="form.oss_setting.domain" class="form-el"></el-input>
        </el-form-item>
      </div>

      <br />
      <el-form-item>
        <el-button type="primary" @click="onSubmit">{{$t('save')}}</el-button>
        <el-button><router-link to="/item/index">{{$t('cancel')}}</router-link></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<style scoped>
.form-el {
  width: 230px;
}
</style>

<script>
export default {
  data () {
    return {
      form: {
        register_open: true,
        ldap_open: false,
        home_page: '1',
        ldap_form: {
          host: '',
          port: '389',
          version: '3',
          base_dn: '',
          bind_dn: '',
          bind_password: '',
          user_field: ''
        },
        home_item: '',
        oss_open: false,
        oss_setting: {
          oss_type: 'aliyun',
          key: '',
          secret: '',
          endpoint: '',
          bucket: '',
          protocol: 'http',
          domain: ''
        }
      },
      itemList: []
    }
  },
  methods: {
    onSubmit () {
      var that = this
      var url = this.DocConfig.server + '/adminSetting/saveConfig'
      var params = new URLSearchParams()
      params.append('register_open', that.form.register_open)
      that.$http.post(url, params).then(function (response) {
        if (response.data.status === 200) {
          that.$message.success('成功')
        } else {
          that.$alert(response.data.message)
        }
      })
    },
    loadConfig () {
      var that = this
      var url = this.DocConfig.server + '/adminSetting/loadConfig'
      var params = new URLSearchParams()
      that.$http.get(url, params).then(function (response) {
        if (response.data.status === 200) {
          if (response.data.data.length === 0) {
            return
          }
          that.form.register_open = response.data.data[0].optionValue === 'true'
          this.form.oss_open = response.data.data.oss_open > 0
          this.form.ldap_open = response.data.data.ldap_open > 0
          this.form.home_page =
            response.data.data.home_page > 0 ? response.data.data.home_page : 1
          this.form.home_item =
            response.data.data.home_item > 0 ? response.data.data.home_item : ''
          this.form.ldap_form = response.data.data.ldap_form
            ? response.data.data.ldap_form
            : this.form.ldap_form
          this.form.oss_setting = response.data.data.oss_setting
            ? response.data.data.oss_setting
            : this.form.oss_setting
        } else {
          this.$alert(response.data.error_message)
        }
      })
    },
    get_item_list () {
      var that = this
      var url = this.DocConfig.server + '/adminItem/getList'
      var params = new URLSearchParams()
      params.append('page', 1)
      params.append('count', 1000)
      that.$http.post(url, params).then(function (response) {
        if (response.data.status === 200) {
          // that.$message.success("加载成功");
          var json = response.data.data
          that.itemList = json
        } else {
          that.$alert(response.data.message)
        }
      })
    }
  },
  mounted () {
    this.get_item_list()
    this.loadConfig()
  },
  beforeDestroy () {
    this.$message.closeAll()
  }
}
</script>
